/*=============================================================================
# GRADUATE STRIPS
==============================================================================*/

.mCSB_container:hover {
  // Enable GPU Accelleration.
  will-change: left;
}


body.home {
  .strip-wrapper {
    display: block;
    margin-bottom: 0;
    .single-grad {
      overflow: hidden;
      float: left;
    }
  }
  .strip-container {
    &::after {
      content: '';
      display: block;
      clear: both;
    }
  }
  @media screen and (min-width: 1301px){
    .single-grad {
      width: 20%;
    }
  }
}

.strip-homepage-text {
  box-shadow: 0 0 .6rem rgba($black, .3);
  font-size: 1.2rem;
  letter-spacing: .4rem;
  text-transform: uppercase;
  color: $actualWhite;
  font-weight: 500;
  padding: 1.5rem 3rem;
  opacity: .5;
  transition: opacity .3s;
  margin: 1rem 0;
  span {
    font-size: 70%;
    opacity: .4;
    margin-top: .6rem;
  }
  &:hover {
    opacity: 1;
  }
  @media screen and (max-width: 600px){
    width: 100%;
    border: 1px solid $actualWhite;
    margin-bottom: 3rem;
  }

  @media screen and (max-width: 400px){
    font-size: 1rem;
    padding: 1.5rem 1rem;
  }
}



.page-template-graduates {
  .polygon {
    z-index: -5;
  }
  .panel--graduate-strips {
    text-align: left;
    height: 100vh;
    padding: 8rem 3rem 3rem;
    text-align: center;
    &::before {
      content: '';
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle;
    }
  }
  .graduates-container {
    width: 98%;
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    opacity: 0;
    transition: opacity 0.6s 0.4s;
  }
  &.loaded .graduates-container {
    opacity: 1;
  }



  .strip {
    white-space: nowrap;
    margin: 0 0 1.5rem;
    font-size: 0;
    background: $actualWhite;
    &:nth-child(3){
      margin-bottom: 0;
    }
  }

  .single-grad {
    @media screen and (min-width: 700px){
      filter: blur(3px);
      opacity: 0.1;
      transition: filter 0.6s;
      display: inline-block !important;
    }
    &.hide {
      opacity: 0 !important;
    }
    &.active {
      filter: none;
      opacity: 1;
    }
  }



}
.strip-multitouch {
    white-space: nowrap;
    margin: 0 0 1.5rem;
    font-size: 0;
    background: $actualWhite;
    text-align: left;
}

.graduates-container-multi {
    width: 98%;
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    -webkit-transition: opacity .6s .4s;
    transition: opacity .6s .4s;
}
.panel-grad-strip-multi {
    height: 100vh;
    padding: 8rem 3rem 3rem;
    text-align: center;
}
.strip-container, .strip {

  .single-grad {
    display: inline-block;
    width: 30vw;
    transition: opacity 0.4s;
    line-height: 0;
    @media screen and (min-width: 800px){
      width: 26vw;
    }
    @media screen and (min-width: 1200px){
      width: 15vw;
    }

    list-style: none;
    position: relative;
    white-space: nowrap;
    background: $actualWhite;
    img, a {
      display: block;
    }

    &:nth-of-type(6){
      display: none;
    }

    .grad-container {

      img {
        transition: filter 0.4s, opacity 0.4s;
      }



      .grad-name {
        padding: .6rem 1.4rem;
        text-align: center;
        position: absolute;
        top: 100%;
        left: 50%;
        color: $actualWhite;
        text-decoration: none;
        font-size: 0.8rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        font-weight: 500;
        transform: translate(-50%, 0);
        transition: all .4s;
        .name {
          position: relative;
          z-index: 5;
          transition: opacity 0.2s;
          opacity: 0;
          display: inline-block;
        }
        .board {
          content: '';
          display: block;
          height: 1rem;
          width: 1rem;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%) rotate(45deg);
          background-color: $black;
          box-shadow: 0 0 0.25em rgba(0,0,0,0.1);
          opacity: 0;
          transition: transform 0.3s 0.2s, width 0.2s, height 0.2s, opacity 0.2s;
        }
      }
      &:hover {
        .grad-name {
          transform: translate(-50%, -3rem);
        }
        .name {
          opacity: 1;
          transition: opacity 0.2s 0.4s;
        }
        .board {
          height: 100%;
          width: 100%;
          opacity: 1;
          transform: translate(-50%,-50%) rotate(540deg);
          transition: transform 0.3s, width 0.4s 0.3s, height 0.4s 0.3s, opacity 0.1s;
        }
      }
      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }

    .grad-container-nohover {

      img {
        transition: filter 0.4s, opacity 0.4s;
      }



      .grad-name-multitouch {
        padding: .6rem 1.4rem;
        text-align: center;
        position: absolute;
        top: 100%;
        left: 50%;
        color: $actualWhite;
        background-color: $black;
        text-decoration: none;
        font-size: 0.7rem;
        text-transform: uppercase;
        letter-spacing: .2rem;
        font-weight: 500;
        transform: translate(-50%, 0);
        transition: all .4s;
        .name-multitouch {
          position: relative;
          z-index: 5;
          transition: opacity 0.2s;
          display: inline-block;
        }
        .board {
          content: '';
          display: block;
          height: 1rem;
          width: 1rem;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%) rotate(45deg);
          background-color: $black;
          box-shadow: 0 0 0.25em rgba(0,0,0,0.1);
          opacity: 0;
          transition: transform 0.3s 0.2s, width 0.2s, height 0.2s, opacity 0.2s;
        }
      }
      .grad-name-multitouch  {
          transform: translate(-50%, -3rem);
        }
        .name-multitouch {
          opacity: 1;
          transition: opacity 0.2s 0.4s;
        }
        .board {
          height: 100%;
          width: 100%;
          opacity: 1;
          transform: translate(-50%,-50%) rotate(540deg);
          transition: transform 0.3s, width 0.4s 0.3s, height 0.4s 0.3s, opacity 0.1s;
        }

      img {
        width: 100%;
        height: auto;
        display: block;
      }
    }


  }
}


@media screen and (max-width: 699px){
  .page-template-graduates {
    .strip {
      margin-bottom: 0;
      white-space: normal;
    }
    .single-grad {
      width: 50%;
      float: left;
      margin-bottom: 2rem;
    }
  }
}



.panel--fam-strips .single-grad:nth-of-type(6){
  display: none;
}

@media (max-width: 1600px) {
  .strip-container .single-grad {
    width: 24.75%;
  }

  .panel--fam-strips .single-grad:nth-of-type(5),
  .panel--fam-strips .single-grad:nth-of-type(6) {
    display: none;
  }
}

@media (max-width: 1300px) {
  .strip-container .single-grad {
    width: 33.33%;
    margin-bottom: 3rem;
  }
  .panel--fam-strips .single-grad:nth-of-type(5),
  .panel--fam-strips .single-grad:nth-of-type(6) {
    display: inline-block;
  }
}

@media (max-width: 950px) {
  .strip-container .single-grad {
    width: 50%;
    margin-bottom: 3rem;
  }
}

@media screen and (max-width: 699px){
  .js .single-grad {
    display: none;
  }
  .js .single-grad.active {
    display: inline-block;
  }
}

@media (max-width: 600px) {
  .strip-container .single-grad {
    margin-bottom: 2rem;
  }
}

@media (max-width: 360px) {
  .strip-container .single-grad .grad-container .grad-name {
    font-size: .9rem;
  }
}



@keyframes gradFadeIn(){
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
